.portfolio__categories{
    display: flex;
    justify-content: center;
    gap: 1.5rem;
    flex-wrap: wrap;
}

.btn.cat__btn{
    text-transform: capitalize;
    transition: var(--transition);
}

.btn.cat__btn:hover{
    transform: translateY(0);
}

.portfolio__projects{
    margin-top: 3rem;
    display: grid;
    grid-template-columns: repeat(3,1fr);
    gap:3rem;
    align-items: start;
}

.card.portfolio__project{
    animation: animateProject 800ms ease-in forwards;
}

@keyframes animateProject {
    from{
        opacity: 0;
    }
    to{
        opacity: 1;
    }
    
}

.portfolio__project{
    background: var(--color-light);
}

.portfolio__project-image{
    width: 100%;
    height: fit-content;
    min-height: 7rem;
    max-height: 15rem;
    background: var(--color-light);
    border-radius: var(--border-radius-2);
    margin-bottom: 1.5rem;
    overflow: hidden;
}

.portfolio__project-image img{
    height: 13rem;
}

.portfolio__project h4{
    height: 3rem;
}

.portfolio__project p{
    margin: 0.5rem 0 2rem;
    height: 8rem;
    overflow:auto;
}

.portfolio__project-cta{
    padding-left: 2rem;
}

.portfolio__project:hover .btn sm{
    background: transparent;
    color:var(--color-primary);
    border-color: var(--color-primary);
}


/**Media Queries for Tablets**/
@media screen and (max-width:1024px){
    .portfolio__projects{
        grid-template-columns: 1fr 1fr;
        gap: 2rem;
    }
}

/**Media Queries for Phones**/
@media screen and (max-width:600px){
    .portfolio__categories{
        gap:0.6rem;
    }
    .btn.cat__btn{
        padding: 0.7rem 0.8rem;
    }
    .portfolio__projects{
        grid-template-columns: 1fr;
        gap: 1.5rem;
    }
}