*{
    margin: 0;
    padding: 0;
    border: 0;
    outline: 0;
    list-style: none;
    text-decoration: none;
    box-sizing: border-box;
}

:root{
    --primary-hue: 270;
    --black-lightness: 15%;
    --dark-lightness: 44%;
    --light-lightness: 94%;
    --white-lightness: 100%;
    --color-primary: hsl(var(--primary-hue),89%,41%);
    --color-white:hsl(0,5%,var(--white-lightness));
    --color-light:hsl(var(--primary-hue),5%,var(--light-lightness));
    --color-black:hsl(var(--primary-hue),5%,var(--black-lightness));
    --color-dark:hsl(var(--primary-hue),5%,var(--dark-lightness));

    /*General Container variables*/
    --container-width-lg:76%;
    --container-width-md:90%;

    /*Border Radius Variables*/
    --border-radius-1:1.2rem;
    --border-radius-2:0.8rem;
    --border-radius-3:0.5rem;

    /*General Transition Variable*/
    --transition: all 500ms ease;
}

html{
    scroll-behavior: smooth;
}

body{
    font-family: 'Montserrat', sans-serif;
    line-height: 1.6;
    font-size: 0.9rem;
}

/*General Container Class*/
.container{
    width: 76%;
    width: var(--container-width-lg);
    margin-inline: auto;
    max-width: 1920px;
}


/*General Section Styles*/
section{
    padding: 10rem 0;
    background: hsl(270,5%,94%);
    background: var(--color-light);
    transition: all 500ms ease;
    transition: var(--transition);
}

section:nth-child(even){
    background: hsl(0,5%,100%);
    background: var(--color-white);
}

section > h2{
    text-align: center;
}

section > p {
    text-align: center;
    margin: 0.5rem auto 4rem;
    width: 40%;
}

a{
   /* color:var(--color-white);*/
    transition: all 500ms ease;
    transition: var(--transition);
}

h1,h2,h3,h4,h5,h6{
    line-height: 1.1;
    color: hsl(270,5%,15%);
    color: var(--color-black);
}

h1{
    font-size: 3rem;
}
h2{
    font-size: 2rem;
}
h3{
    font-size: 1.6rem;
}
h4{
    font-size: 1.1rem;
}
h5{
    font-size: 0.95rem;
}
h6{
    font-size: 0.8rem;
}

/*General Image Styles*/
img{
    display: block;
    width: 100%;
    object-fit: cover;
}

/*General Button Styles*/
.btn{
    display: inline-block;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    display: flex;
    align-items: center;
    grid-gap: 4rem;
    gap: 4rem;
    background: hsl(270,5%,94%);
    background: var(--color-light);
    color:hsl(270,5%,15%);
    color:var(--color-black);
    padding: 0.6rem 2.5rem;
    border: 2px solid transparent;
    border-radius: 0.8rem;
    border-radius: var(--border-radius-2);
    cursor: pointer;
}

.btn:hover{
    background-color: transparent;
    border-color: hsl(270,5%,94%);
    border-color: var(--color-light);
    -webkit-transform: translateY(-0.5rem);
            transform: translateY(-0.5rem);
}

.btn.white{
    background: hsl(0,5%,100%);
    background: var(--color-white);
}

.btn.white:hover{
    background:transparent;
    border-color: hsl(0,5%,100%);
    border-color: var(--color-white);
}

.btn.primary{
    background: hsl(270,89%,41%);
    background: var(--color-primary);
    color:white;
}

.btn.primary:hover{
    background: transparent;
    border-color: hsl(270,89%,41%);
    border-color: var(--color-primary);
    color:hsl(270,89%,41%);
    color:var(--color-primary)
}

.btn.sm{
    padding: 0.5rem,1.2rem;
}

/* MEDIA QUERIES (tablets) */
@media screen and (max-width: 1024px) {
    body {
        font-size: 1rem;
    }
    
    .container {
        width: 90%;
        width: var(--container-width-md);
    }

    section {
        padding: 8rem 0;
    }

    section > p {
        width: 72%;
        margin-bottom: 3rem;
    }

    h1 {
        font-size: 2rem;
    }
}





/* MEDIA QUERIES (phones) */
@media screen and (max-width: 600px) {
    h1 {
        font-size: 2.4rem;
    }

    h2 {
        font-size: 1.8rem;
    }

    h3 {
        font-size: 1.4rem;
    }

    h4 {
        font-size: 1rem;
    }

    .btn {
        padding: 0.6rem 1.5rem;
    }

    section > p {
        width: 90%;
        width: var(--container-width-md);
    }
}

nav{
    width:100vw;
    height: 5rem;
    display: grid;
    place-items: center;
    background: var(--color-primary);
    position: fixed;
    top:0;
    left:0;
    z-index: 9;
    box-shadow: 0 1rem 1.5rem hsla(var(--primary-hue),68%,42%,20%); 
    transition: var(--transition);
}


.nav__container{
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.nav__logo{
    width: 2.5rem;
    height: 2.5rem;
    border-radius: 50%;
    overflow: hidden;
}

.nav__menu{
    display: flex;
    grid-gap: 3.5rem;
    gap: 3.5rem;
}

.nav__menu a{
    color:white
}

#theme__icon{
    background: transparent;
    color: white;
    font-size: 2rem;
    cursor: pointer;
}


header{
    margin-top: 5rem;
    height: calc(100vh-5rem);
    display: grid;
    place-items: center;
    background: var(--color-white);
    transition: var(--transition);
}

.header__container{
    display: flex;
    flex-direction: column;
    align-items: center;
    position: relative;
    margin:1rem;
    
}

.header__profile{
    width: 18rem;
    height: 22rem;
    border-radius: 9rem 9rem 2rem 2rem;
    overflow: hidden;
    margin-bottom: 1.5rem;
}

.header__container p{
    width: 35rem;
    text-align:justify;
    margin: 0.6rem 0 2rem;
}

.header__cta{
    display: flex;
    align-items: center;
    grid-gap:3.5rem;
    gap:3.5rem;
}

.header__socials{
    position: absolute;
    left: 0;
    bottom: 0;
    display: flex;
    flex-direction: column;
    grid-gap:1rem;
    gap:1rem;
}

.header__socials a{
    width: 1.8rem;
    aspect-ratio: 1/1;
    background: var(--color-light);
    color: var(--color-black);
    display: grid;
    place-items: center;
    border-radius: var(--border-radius-3);
}

.header__socials a:hover{
    background: var(--color-primary);
    color: car(--color-white);
    -webkit-transform: translateX(-0.5rem);
            transform: translateX(-0.5rem);
}


/*Media Queries*/
@media screen and (max-width:1024px){
    header{
        margin-top: 0;
        height: 100vh;
    }
}

@media screen and (max-width:600px){
    .header__profile{
        width: 14rem;
        height: 17rem;
        max-width: 60%;
        max-height: 35vh;
    }

    .header__container p{
        width: 100%;

    }

    .header__cta{
        grid-gap:1rem;
        gap:1rem;
    }
    .header__socials{
        display: none;
    }
}
.about__container{
    display: grid;
    grid-template-columns: 35% 53%;
    grid-gap:12%;
    gap:12%
}

.about__potrait{
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    max-height: 40rem;
    border: 1rem solid var(--color-white);
    transition: var(--transition);
}

.about__potrait:hover{
    border-width: 0;
}

.about__cards{
    margin: 3rem 0 1.5rem;
    display: grid;
    grid-template-columns: repeat(3,1fr);
    grid-gap:1.5rem;
    gap:1.5rem
}

.card.about__cards{
    text-align: center;
}

.about__card-icon{
    font-size: 1.3rem;
    color:var(--color-black);
    border-radius: var(--border-radius-3);
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    margin-inline: auto;
    padding: 0.6rem;
    display: flex;
    align-items: center;
    transition: var(--transition);
}

.card.about__card:hover .about__card-icon{
    background: var(--color-primary);
    color: var(--color-white);
}

.card.about__card h5{
    margin: 1rem 0;
}

.about__right p{
    margin-bottom: 0.8rem;
    text-align: justify;
}

.about__right .btn{
    margin-top: 2.8rem;
}

/* MEDIA QUERIES (tablets) */
@media screen and (max-width: 1024px) {
    .about__left {
        display: none;
    }

    .about__container {
        grid-template-columns: 1fr;
    }

    .about__right h2 {
        text-align: center;
    }
}



/* MEDIA QUERIES (phones) */
@media screen and (max-width: 600px) {

.about__cards {
    grid-template-columns: 1fr 1fr;
    grid-gap: 1rem;
    gap: 1rem;
}

.about__right .btn {
    margin-top: 2rem;
}



}
.card{
    background: var(--color-white);
    padding: 1.5rem;
    border: 2px solid transparent;
    border-radius: var(--border-radius-1);
    transition: var(--transition)
}

.card:hover{
    background: transparent;
    border-color: var(--color-white);
    -webkit-transform: translateY(-0.5rem);
            transform: translateY(-0.5rem);
}

.card.light{
    background: var(--color-light);
}

.card.light:hover{
    background: transparent;
    border-color: var(--color-light);
}

/*Media Queries (phones)*/
@media screen and (max-width:600px){
    .card{
        padding: 1.2rem;
    }
}
.portfolio__categories{
    display: flex;
    justify-content: center;
    grid-gap: 1.5rem;
    gap: 1.5rem;
    flex-wrap: wrap;
}

.btn.cat__btn{
    text-transform: capitalize;
    transition: var(--transition);
}

.btn.cat__btn:hover{
    -webkit-transform: translateY(0);
            transform: translateY(0);
}

.portfolio__projects{
    margin-top: 3rem;
    display: grid;
    grid-template-columns: repeat(3,1fr);
    grid-gap:3rem;
    gap:3rem;
    align-items: start;
}

.card.portfolio__project{
    -webkit-animation: animateProject 800ms ease-in forwards;
            animation: animateProject 800ms ease-in forwards;
}

@-webkit-keyframes animateProject {
    from{
        opacity: 0;
    }
    to{
        opacity: 1;
    }
    
}

@keyframes animateProject {
    from{
        opacity: 0;
    }
    to{
        opacity: 1;
    }
    
}

.portfolio__project{
    background: var(--color-light);
}

.portfolio__project-image{
    width: 100%;
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    min-height: 7rem;
    max-height: 15rem;
    background: var(--color-light);
    border-radius: var(--border-radius-2);
    margin-bottom: 1.5rem;
    overflow: hidden;
}

.portfolio__project-image img{
    height: 13rem;
}

.portfolio__project h4{
    height: 3rem;
}

.portfolio__project p{
    margin: 0.5rem 0 2rem;
    height: 8rem;
    overflow:auto;
}

.portfolio__project-cta{
    padding-left: 2rem;
}

.portfolio__project:hover .btn sm{
    background: transparent;
    color:var(--color-primary);
    border-color: var(--color-primary);
}


/**Media Queries for Tablets**/
@media screen and (max-width:1024px){
    .portfolio__projects{
        grid-template-columns: 1fr 1fr;
        grid-gap: 2rem;
        gap: 2rem;
    }
}

/**Media Queries for Phones**/
@media screen and (max-width:600px){
    .portfolio__categories{
        grid-gap:0.6rem;
        gap:0.6rem;
    }
    .btn.cat__btn{
        padding: 0.7rem 0.8rem;
    }
    .portfolio__projects{
        grid-template-columns: 1fr;
        grid-gap: 1.5rem;
        gap: 1.5rem;
    }
}
.contact__container{
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    display: flex;
    justify-content: center;
    grid-gap: 3rem;
    gap: 3rem;
}

.contact__container a{
    width: 5rem;
    aspect-ratio: 1/1;
    color: white;
    background: var(--color-primary);
    display: grid;
    place-items: center;
    font-size: 2rem;
    border: 2px solid transparent;
    border-radius: var(--border-radius-1);
}

.contact__container a:hover{
    background: transparent;
    border-color: var(--color-primary);
    color: var(--color-primary);
    -webkit-transform: translateY(-0.5rem);
            transform: translateY(-0.5rem);
}


/*Media Queries for phones*/
@media screen and (max-width:600px) {
    .contact__container{
        grid-gap:1.5rem;
        gap:1.5rem;
    }
}
footer {
    background: var(--color-primary);
    padding-top: 8rem;
    box-shadow: 0 -1rem 1.5rem hsla(var(--primary-hue), 68%, 42%, 20%);
    transition: var(--transition);
}

footer .nav__menu {
    justify-content: center;
}

.footer__socials {
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    display: flex;
    grid-gap: 1.5rem;
    gap: 1.5rem;
    margin: 3rem auto 4rem;
    padding-bottom: 2rem;
}

.footer__socials a {
    background: var(--color-black);
    color: var(--color-white);
    padding: 0.5rem;
    border-radius: var(--border-radius-3);
}

.footer__socials a:hover {
    opacity: 0.7;
    -webkit-transform: translateY(-0.5rem);
            transform: translateY(-0.5rem);
}

.footer__copyright {
    color: white;
    text-align: center;
    border-top: 2px solid rgba(0, 0, 0, 0.2);
    padding: 1.5rem 0;
}

